.square {
  width: 100px;
  height: 100px;
  border: 1px solid #222222;
  background-color: #fff;
  cursor: pointer;
  outline: none;
  font-size: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.square:hover {
  background-color: #f0f0f0;
  transition: 0.3s all ease;
}